@import "./utilities.scss";
@import "./display.scss";

.button {
  background: var(--background-2);
  border: var(--button-border);
  color: var(--foreground-1);
  line-height: 1rem;
  width: fit-content;
  @include hover-bg;
  @include focus-bg;
  @include focus-ring;
  @include bold-text;
  @include highlight-shadow;
}

.button-reset {
  background: unset;
  box-shadow: unset;
  border: unset;
}

.fill-space {
  width: 100%;
  display: block;
}

.with-icon {
  svg {
    width: 1rem;
    height: 1rem;
    margin-right: var(--space-3);
  }
}

.rtl {
  .with-icon {
    svg {
      margin: 0 0 0 var(--space-2);
    }
  }
}